* {
    font-family: 'Open Sans', sans-serif !important;
  }

  .full-container {
    height: 100vh;
    background: -webkit-gradient(linear, left top, right top, from(rgb(54, 20, 166)), to(rgb(33, 162, 233)));
    background: -webkit-linear-gradient(left, rgb(54, 20, 166) 0%, rgb(33, 162, 233) 100%);
    background: -o-linear-gradient(left, rgb(54, 20, 166) 0%, rgb(33, 162, 233) 100%);
    background: linear-gradient(to right, rgb(54, 20, 166) 0%, rgb(33, 162, 233) 100%);
  }



